body{
  background:#999;
  font-family: 'Orbitron', sans-serif;
}
#overAll{
  height:90vh;
  width:50vw;
  background-color:black;
  display:flex;
  margin: 5vh 25vw ;
  padding:0;
  border-radius:2vh;
  
}
#inner{
  height:98%;
  width:100%;
  background-color:#333;
  padding:0;
  margin: 1vh;
  border-radius:2vh;
  
  
}
#buttons{
  height:74%;
  width:95%;
  background-color:#333;
  padding:0;
  margin-left: 3.8%;
  border-radius:2vh;
}
.whole-row{
  
  height:18%;
  
 
}
.one-block{
  background-color:grey;
  height:18%;
  width:24%;
  margin-left:0.2%;
  font-size:9vh;
  text-align:center;
  color:orange;
  padding:0;
  border-radius:2vh;
  
}
#clear{
  background-color:red;
  color:black;
}
#display{
  background-color:black;
  color:orange;
  font-size:4.5vh;
  text-align:center;
  margin-top:1%;
  margin-bottom:1%;
  margin-left:4%;
  width:91.77%;
  border-radius:2vh;
  overflow:hidden;
  text-overflow:ellipsis;
  
}
#equals{
  background-color:green;
  text-align:center;
  font-size:9vh;
  margin-left:0.2%;
  width:96.6%;
  border-radius:2vh;
  color:black;
  
  
   
}
.dispKey{
  margin-top:0.6vh;
  margin-bottom:0.6vh;
   overflow:hidden;
  text-overflow:ellipsis;
}
